import React, { useEffect, useState } from "react";
import styled from "styled-components";
import feathersClient from "feathersClient";

const Container = styled.fieldset`
    grid-column-end: span 2;
    grid-row-end: span 2;
    display: grid;
    gap: 1px;
`;

const Modelo = function ({ inputs, setInputs, onChange }) {
    const [fabricantes, setFabricantes] = useState({
        total: 0,
        limit: 0,
        skip: 0,
        data: [
            {
                id: 0,
                nombre: "",
                createdAt: "",
                updatedAt: "",
            },
        ],
    });
    const [modelos, setModelos] = useState({
        total: 0,
        limit: 0,
        skip: 0,
        data: [
            {
                id: 0,
                nombre: "",
                createdAt: "",
                updatedAt: "",
                fabricanteId: 0,
            },
        ],
    });

    useEffect(() => {
        inputs.fabricanteId === "0" &&
            inputs.modeloId !== "0" &&
            feathersClient
                .service("modelos")
                .get(inputs.modeloId)
                .then((modelo) => {
                    setInputs((inputs) => ({
                        ...inputs,
                        fabricanteId: modelo.fabricanteId.toString(),
                    }));
                })
                .catch((error) => {
                    console.error(error);
                });
        feathersClient
            .service("fabricantes")
            .find({
                query: {
                    $limit: 100,
                    $sort: {
                        nombre: 1,
                    },
                },
            })
            .then((fabricantes) => {
                setFabricantes(fabricantes);
                inputs.fabricanteId !== "0" &&
                    feathersClient
                        .service("modelos")
                        .find({
                            query: {
                                fabricanteId: inputs.fabricanteId,
                                $limit: 200,
                                $sort: {
                                    nombre: 1,
                                },
                            },
                        })
                        .then((found) => {
                            setModelos(found);
                            inputs.modeloId === "0" &&
                                setInputs((inputs) => ({
                                    ...inputs,
                                    modeloId: found.data[0].id.toString(),
                                }));
                        })
                        .catch((error) => {
                            console.error(error);
                        });
            })
            .catch((error) => {
                console.error(error);
            });
    }, [inputs.fabricanteId, inputs.modeloId, setInputs]);

    return (
        <Container>
            <label>
                Marca
                <select
                    name="fabricanteId"
                    value={inputs.fabricanteId}
                    onChange={onChange}
                >
                    <option value={0}></option>
                    {fabricantes.data.map((aFabricante) => (
                        <option key={aFabricante.id} value={aFabricante.id}>
                            {aFabricante.nombre}
                        </option>
                    ))}
                </select>
            </label>
            <label>
                Modelo
                <select
                    name="modeloId"
                    value={inputs.modeloId}
                    onChange={onChange}
                >
                    {modelos.data.map((aModelo) => (
                        <option key={aModelo.id} value={aModelo.id}>
                            {aModelo.nombre}
                        </option>
                    ))}
                </select>
            </label>
        </Container>
    );
};

export default Modelo;
