import React, { useEffect, useState } from "react";
import feathersClient from "feathersClient";
import styled from "styled-components";

import Modelo from "./Modelo";
import Card from "components/Card";

const Box = styled.article`
    display: grid;
    grid-auto-flow: column;
    grid-template-columns: 4.25rem 1fr;
    gap: 1rem;

    h4 {
        text-align: right;
    }

    div {
        display: grid;
        gap: 0.5rem;
    }
`;

const Form = styled.aside`
    grid-template-columns: 1fr 1fr 1fr;
`;

const Wide = styled.label`
    grid-column-end: span 2;
`;

const Vehiculo = function ({
    matchModelo,
    active,
    setActiveId,
    onClick,
    edit,
    setEdit,
    vehiculo,
}) {
    const [inputs, setInputs] = useState({
        patente: "",
        year: "",
        combustible: "NAFTA",
        cilindrada: "",
        vin: "",
        clienteId: 0,
        modeloId: "0",
        fabricanteId: "0",
    });
    const [error, setError] = useState("");
    const [message, setMessage] = useState("");

    function validate(inputs) {
        let error = "";
        inputs.modeloId === "0"
            ? (error = "Seleccione un modelo")
            : inputs.patente === ""
            ? (error = "Ingrese la patente")
            : inputs.year === ""
            ? (error = "Ingrese el a√±o")
            : inputs.vin === ""
            ? (error = "")
            : /\s/.test(inputs.vin)
            ? (error = "El vin no puede contener espacios")
            : inputs.vin && inputs.vin.includes("O")
            ? (error = "El vin no puede contener la letra O")
            : inputs.vin && inputs.vin.includes("I")
            ? (error = "El vin no puede contener la letra I")
            : inputs.vin && inputs.vin.includes("Q")
            ? (error = "El vin no puede contener la letra Q")
            : inputs.vin && inputs.vin.length !== 17
            ? (error = "El VIN debe contener 17 caracteres")
            : (error = "");
        setError(error);
        return error;
    }

    const handleCreate = (event) => {
        event.preventDefault();
        validate(inputs) === "" &&
            feathersClient
                .service("vehiculos")
                .create({
                    patente: inputs.patente,
                    year: inputs.year,
                    combustible:
                        inputs.combustible === "NAFTA"
                            ? "Nafta"
                            : inputs.combustible === "DIESEL"
                            ? "Diesel"
                            : inputs.combustible,
                    cilindrada: inputs.cilindrada,
                    vin: inputs.vin,
                    clienteId: inputs.clienteId,
                    modeloId: inputs.modeloId,
                    createdAt: Date(),
                    updatedAt: Date(),
                })
                .then((data) => {
                    setEdit(false);
                    setActiveId(data.id);
                    setMessage("Cliente creado");
                    setTimeout(function () {
                        setMessage("");
                    }, 3000);
                })
                .catch((error) => {
                    console.error(error);
                });
    };

    const handleEdit = (event) => {
        event.preventDefault();
        validate(inputs) === "" &&
            feathersClient
                .service("vehiculos")
                .patch(vehiculo.id, {
                    patente: inputs.patente,
                    year: inputs.year,
                    combustible:
                        inputs.combustible === "NAFTA"
                            ? "Nafta"
                            : inputs.combustible === "DIESEL"
                            ? "Diesel"
                            : inputs.combustible,
                    cilindrada: inputs.cilindrada,
                    vin: inputs.vin,
                    clienteId: inputs.clienteId,
                    modeloId: inputs.modeloId,
                })
                .then(() => {
                    setEdit(false);
                    setMessage("Cliente guardado");
                    setTimeout(function () {
                        setMessage("");
                    }, 5500);
                })
                .catch((error) => {
                    console.error(error);
                });
    };

    const handleDelete = (event) => {
        event.preventDefault();
        setActiveId(0);
        feathersClient
            .service("vehiculos")
            .remove(vehiculo.id)
            .then(() => {})
            .catch((error) => {
                console.error(error);
            });
    };

    const handleInputChange = (event) => {
        event.persist();
        setInputs((inputs) => ({
            ...inputs,
            [event.target.name]:
                event.target.name === "patente" || "vin"
                    ? event.target.value.toUpperCase()
                    : event.target.value,
        }));
    };

    useEffect(() => {
        setInputs({
            patente: vehiculo.patente,
            year: vehiculo.year || "",
            combustible: vehiculo.combustible.toUpperCase(),
            cilindrada: vehiculo.cilindrada || "",
            vin: vehiculo.vin || "",
            clienteId: vehiculo.clienteId,
            modeloId: vehiculo.modeloId.toString(),
            fabricanteId: "0",
        });
    }, [vehiculo]);

    return (
        <>
            <Card
                type="Veh√≠culo"
                message={message}
                active={active}
                edit={edit}
                setEdit={setEdit}
                create={vehiculo.id === 0 ? true : false}
                onSubmit={vehiculo.id === 0 ? handleCreate : handleEdit}
                onReset={handleDelete}
            >
                {vehiculo.id !== 0 && (
                    <Box onClick={onClick}>
                        <h4>{vehiculo.patente}</h4>
                        <div>
                            <h6>{matchModelo(vehiculo.modeloId)}</h6>
                            <p>
                                {vehiculo.combustible} {vehiculo.cilindrada}
                                <small>{vehiculo.year}</small>
                            </p>
                        </div>
                        <p>{vehiculo.vin}</p>
                    </Box>
                )}
                <Form>
                    <Modelo
                        inputs={inputs}
                        setInputs={setInputs}
                        onChange={handleInputChange}
                    />
                    <label>
                        Combustible
                        <select
                            name="combustible"
                            value={inputs.combustible}
                            onChange={handleInputChange}
                        >
                            <option value="NAFTA">Nafta</option>
                            <option value="DIESEL">Diesel</option>
                            <option value="GNC">GNC</option>
                        </select>
                    </label>
                    <label>
                        Cilindrada
                        <input
                            type="number"
                            min="0.1"
                            max="20"
                            step="0.1"
                            name="cilindrada"
                            placeholder="-"
                            value={inputs.cilindrada}
                            onChange={handleInputChange}
                        />
                    </label>
                    <label>
                        A√±o
                        <input
                            type="number"
                            min="1900"
                            max="9999"
                            name="year"
                            placeholder="-"
                            value={inputs.year}
                            onChange={handleInputChange}
                        />
                    </label>
                    <Wide>
                        {error === "" ? "VIN" : error}
                        <input
                            type="text"
                            minLength={17}
                            name="vin"
                            placeholder="-"
                            autoComplete="off"
                            value={inputs.vin}
                            onChange={handleInputChange}
                            required
                        />
                    </Wide>
                    <label>
                        {error === "" ? "Patente" : error}
                        <input
                            type="text"
                            name="patente"
                            placeholder="-"
                            autoComplete="off"
                            value={inputs.patente}
                            onChange={handleInputChange}
                            required
                        />
                    </label>
                    <Wide>
                        Propietario
                        <select
                            disabled
                            name="clienteId"
                            value={inputs.clienteId}
                            onChange={handleInputChange}
                        >
                            <option value="">{inputs.clienteId}</option>
                        </select>
                    </Wide>
                </Form>
            </Card>
        </>
    );
};

export default Vehiculo;
